@import "~arui-feather/vars/color.css";
@import "~arui-feather/vars/font.css";
@import "~arui-feather/vars/color.css";
@import "~arui-feather/vars/primitive-colors.css";
@import "~arui-feather/vars/border.css";

:root {
  --color-white-05: rgba(255, 255, 255, 0.05);
  --color-white-24: rgba(255, 255, 255, 0.24);
  --color-white-70: rgba(255, 255, 255, 0.7);
  --content-width: 1092px;

  --input-background: rgba(11, 31, 53, 0.05);

  /**
   * Брейкпоинты
   */
  --mobile-s: 320px;
  --mobile-m: 375px;
  --mobile-l: 412px;
  --tablet-s: 600px;
  --tablet-m: 768px;
  --desktop-s: 1024px;
  --desktop-m: 1280px;
  --desktop-l: 1440px;
  --desktop-xl: 1920px;

  /**
   * Система отступов
   * https://github.com/alfa-laboratory/arui-feather/blob/master/GUIDE.md
   */
  --gap-2xs: 4px;
  --gap-xs: 8px;
  --gap-s: 12px;
  --gap-m: 16px;
  --gap-l: 20px;
  --gap-xl: 24px;
  --gap-2xl: 32px;
  --gap-3xl: 40px;
  --gap-4xl: 48px;
  --gap-5xl: 72px;

  /**
   * Отрицательные отступы
   */
  --gap-2xs-neg: -4px;
  --gap-xs-neg: -8px;
  --gap-s-neg: -12px;
  --gap-m-neg: -16px;
  --gap-l-neg: -20px;
  --gap-xl-neg: -24px;
  --gap-2xl-neg: -32px;
  --gap-3xl-neg: -40px;
  --gap-4xl-neg: -48px;
  --gap-5xl-neg: -72px;
}
